.footer {
    text-align: center;
    padding: 50px 20px;
    max-width: 700px;
    margin: 0 auto;
    background-color: #282c34;
    color: white;
}

.footer-button {
    padding: 10px 20px;
    font-size: 1rem;
    background-color: #6a4c42;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s;
}

.footer-button:hover {
    background-color: #796151;
}

.footer-button:active {
    background-color: #bb9d88;
}

.footer-details {
    max-width: 700px;
    margin: 20px auto;
    padding: 15px;
    background-color: #f5f0e6;
    color: #3b3028;
    border-radius: 8px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    animation: slide-down 1s ease-out forwards, color-change 2s ease-in-out forwards;
    transform-origin: top center;
}

/* スライドアニメーション */
@keyframes slide-douwn {
    0% {
        opacity: 0;
        transform: translateY(-10px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

/* カラーアニメーション */
@keyframes color-change {
    0% {
        background-color: #6c7d8e;
        color: #6f8aff;
    }
    50% {
        background-color: #e4d4c3;
        color: #3a2e2a;
    }
    100% {
        background-color: #d4d8a7;
        color: #2f2520;
    }
}