.header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #282c34;
    color: white;
    width: 100%;
    box-sizing: border-box;
}

.header-title {
    font-size: 2.3rem;
    margin-top: 10px;
    opacity: 0;
    animation: fade-in 3s ease-out forwards;
}

.header-subtitle {
    font-size: 1.2rem;
    margin-top: 10px;
}

/* アニメーション定義*/
@keyframes fade-in {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}